import { Component, Prop } from 'vue-property-decorator'
import { Properties } from 'csstype'
import { EventsOn } from '@badam/vue-tsx-support'
import Loading from '../components/loading'
import BaseComponent from './index'

interface OuterProps {
  styles?: Properties
}

interface InnerProps {
  isLoading?: boolean
  draggable?: boolean
}

type Events = {
  [P in keyof EventsOn]: (payload: EventsOn[P]) => void
}

type Props = OuterProps & InnerProps & Partial<Events>

@Component({})
class BaseView<T = {}> extends BaseComponent<Props & T> {
  static compName = 'BaseView'

  @Prop()
  styles!: Properties

  render(): JSX.Element | null {
    return (
      <div style={[this.styles]}>
        {this.$slots.default}
        {this.isLoading ? (
          <Loading
            style={{
              position: 'absolute',
              transform: 'translate(-50%,-50%)',
              left: '50%',
              top: '50%',
              zIndex: 999,
            }}
          ></Loading>
        ) : null}
      </div>
    )
  }
}

BaseView.install = (Vue) => {
  Vue.component(BaseView.compName, BaseView)
}

export default BaseView
